body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0px;
}
ol, ul {
  padding-left: 30px;
}
.action-button{
  background: none;
  border: 1px solid #999;
  border-radius: 3px;
  font-size: 40px;
  margin: 0 3px 0 3px;
  display: inline;
}
.action-button:hover{
  background-color: #61dafb;
}

#container{
  margin: 20px;
}

.game-status {
  margin-bottom: 14px;
  font-size: 40px;  
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 90px;
  font-weight: bold;
  line-height: 34px;
  min-height: 100px;
  width: 33.3%;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  display: table-cell;
}

.winner.square{
  background: #ffff00;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  margin: auto;
}

.game-board{
  width: 100%;
}

.board-row{
  display: table;
  width: 100%;
  margin-bottom: -2px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.game-info {
  margin-left: 20px;
  margin-top: 20px;
}

.game-info > div,
.move{
  font-weight: 600;  
}

.move > a{
  text-decoration: none;
  color: black;
}

.active.move,
.active.move > a{
  color: #61dafb !important;
}

.special-thanks{
  text-align: center;
  margin-top: 20px;
  font-size: 40px;
  font-weight: 600;
}

.red{
  color: #ff0000;
}

.fa-heart{
  -webkit-animation: Heart-Rate infinite 1s ease;
          animation: Heart-Rate infinite 1s ease
}

@-webkit-keyframes Heart-Rate {
  from { -webkit-transform: scale(1); transform: scale(1); }
  to { -webkit-transform: scale(1.1); transform: scale(1.1); }
}

@keyframes Heart-Rate {
  from { -webkit-transform: scale(1); transform: scale(1); }
  to { -webkit-transform: scale(1.1); transform: scale(1.1); }
}

@media(min-width: 950px){
  .game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 60%;
    margin: auto;
  }
  .game-board{
    width: 450px;
  }
  .game-info{
    width: 300px;
    margin-top: 0;
  }
  .square{
    min-height: 150px;
    font-size: 140px;
  }
}

@media(max-width: 949px){
  ol, ul, .sort-button {
    display: none;
  }  
  .game-info{
    text-align: center;
    margin-left: 0;
  }
  .special-thanks{
    font-size: 20px;
  }
}


